@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro:200,400);
body {
  margin: 0;
  font-family: "Source Sans Pro";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: linear-gradient(
    100deg,
    rgba(250, 214, 195, 0.8) 30%,
    #b0eae8 120%
  );
}
.Home-dog img {
  width: 75%;
  border-radius: 50%;
  -webkit-filter: grayscale(1);
          filter: grayscale(1);
  box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.1);
  -webkit-transition: width 0.4s cubic-bezier(0.215, 0.61, 0.355, 1), -webkit-filter 0.4s cubic-bezier(0.215, 0.61, 0.355, 1);
  transition: width 0.4s cubic-bezier(0.215, 0.61, 0.355, 1), -webkit-filter 0.4s cubic-bezier(0.215, 0.61, 0.355, 1);
  transition: filter 0.4s cubic-bezier(0.215, 0.61, 0.355, 1), width 0.4s cubic-bezier(0.215, 0.61, 0.355, 1);
  transition: filter 0.4s cubic-bezier(0.215, 0.61, 0.355, 1), width 0.4s cubic-bezier(0.215, 0.61, 0.355, 1), -webkit-filter 0.4s cubic-bezier(0.215, 0.61, 0.355, 1);
}

.Home-dog:hover img {
  -webkit-filter: grayscale(0);
          filter: grayscale(0);
}

.Home-dog .underline {
  text-decoration: none;
  border: 4px solid transparent;
  display: inline-block;
  vertical-align: top;
  line-height: 36px;
  text-transform: uppercase;
  color: black;
  letter-spacing: 0.2em;
  text-align: center;
  font-size: 1.4rem;
  margin: 10px;
  padding: 0 1rem;
  position: relative;
}

.Home-dog .underline::after {
  content: '';
  position: absolute;
  width: 0%;
  height: 4px;
  display: block;
  background-color: #fff;
  bottom: 0;
  left: 50%;
  -webkit-transition: left 0.4s cubic-bezier(0.215, 0.61, 0.355, 1), width 0.4s cubic-bezier(0.215, 0.61, 0.355, 1);
  transition: left 0.4s cubic-bezier(0.215, 0.61, 0.355, 1), width 0.4s cubic-bezier(0.215, 0.61, 0.355, 1);
}

.Home-dog:hover .underline::after {
  width: 100%;
  left: 0;
}
.Dog-card {
  box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.1);
}

.Dog-card img {
  -webkit-filter: grayscale(1);
          filter: grayscale(1);
  -webkit-transition: width 0.4s cubic-bezier(0.215, 0.61, 0.355, 1), -webkit-filter 0.4s cubic-bezier(0.215, 0.61, 0.355, 1);
  transition: width 0.4s cubic-bezier(0.215, 0.61, 0.355, 1), -webkit-filter 0.4s cubic-bezier(0.215, 0.61, 0.355, 1);
  transition: filter 0.4s cubic-bezier(0.215, 0.61, 0.355, 1), width 0.4s cubic-bezier(0.215, 0.61, 0.355, 1);
  transition: filter 0.4s cubic-bezier(0.215, 0.61, 0.355, 1), width 0.4s cubic-bezier(0.215, 0.61, 0.355, 1), -webkit-filter 0.4s cubic-bezier(0.215, 0.61, 0.355, 1);
}

.Dog-card:hover img {
  -webkit-filter: grayscale(0);
          filter: grayscale(0);
}
